<style scoped>
  .uk-dropdown-nav {
      font-size: .975rem;
  }



  .uk-input, .uk-select:not([multiple]):not([size]) {
      height: 35px;
      vertical-align: middle;
      display: inline-block;
  }

  .uk-grid-small > div{
    margin-bottom:0px;
  }

  .uk-form-label {
      font-size: .56rem;
      text-transform: uppercase;
      letter-spacing: 1px;
  }

</style>


<template>

    <div style="font-size: 0.7rem;background: #fbfbfb;;display:grid;grid-template-columns:1fr;width:100%;place-items:start center;overflow-y:overlay" class="custom-scroll-bar">



      <div class="stage" style="display: grid;place-items: stretch;grid-template-rows: 40px 1fr;padding: 15px;overflow-y: hidden;color: rgb(51, 51, 51);">

          <div class="adk_grid_toolbar" style="grid-template-columns:auto 1fr;margin-right: 50px;margin-left: 50px;border-bottom: 1px solid #e0e0e0;">

              <div style="display: grid;place-self: center start;text-align: left;">
                  <div style="display:grid;place-self:center start;padding-left:0px;padding-right:20px;color:rgba(61, 61, 61, 0.78) !important;grid-template-columns:auto auto;font-size: 1rem;line-height: 35px;">
                     <span style="font-size: 1rem; font-weight: bold;letter-spacing:0.5px">
                      Edit Report
                     </span>
                  </div>
              </div>
              <div style="display: grid;grid-gap: 10px;grid-template-columns: auto auto;place-self: center end;text-align: right;">
              </div>
          </div>

          <div class="" style="padding-top: 30px;padding-left: 50px;padding-right: 50px;">

              <form autocomplete="off" class="uk-grid-small uk-grid" uk-grid onsubmit="return false;">

                  <div class="uk-width-1-2@s uk-grid-margin uk-first-column ">
                      <label class="uk-form-label" for="form-stacked-text">Report Name</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <input tabindex="3" id="name" type="text" class="uk-input"  placeholder=""  v-model="reportObject.name" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>
                  <div class="uk-width-1-2@s uk-grid-margin"></div>

                  <div class="uk-width-1-2@s uk-grid-margin uk-first-column " style="z-index: 1;">
                      <div style="height: 70px;">
                           <label class="uk-form-label">Report Group</label>
                           <div class="uk-form-controls">
                               <ui-selectbox-single v-bind:availableItems="availableReportGroups"
                                                    name="Roles"
                                                    tagProperty="name"
                                                    displayLabelProps="name" maxTagsToDisplay="3"
                                                    searchProperty="name"
                                                    data_tabindex="4"
                                                    disableAcronym="true"
                                                    v-on:selectBoxUpdate="updateReportGroupEmitEvent($event)"/>
                           </div>
                      </div>
                  </div>
                  <div  class="uk-width-1-2@s uk-grid-margin uk-first-column">
                      <label class="uk-form-label" for="form-stacked-text">Status</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <select  tabindex="6"  class="uk-select" v-model="reportObject.status" style="font-family: Lato;font-weight: 300;height: 35px;">
                                  <option value="ACTIVE">ACTIVE</option>
                                  <option value="SUSPENDED">SUSPENDED</option>
                              </select>
                          </div>
                      </div>
                  </div>

                  <div  class="uk-width-1-1">
                        <label class="uk-form-label" for="form-stacked-text">Description</label>
                        <textarea class="uk-textarea" rows="2" placeholder="" id="description"  v-model="reportObject.description"></textarea>
                  </div>

                  <div class="uk-width-1-2@s uk-grid-margin uk-first-column ">
                      <label class="uk-form-label" for="form-stacked-text">Report File Name</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <input tabindex="7" id="reportFileName" type="text" class="uk-input"  placeholder=""  v-model="reportObject.reportFileName" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>
                  <div class="uk-width-1-2@s uk-grid-margin"></div>

                  <div class="uk-width-1-2@s uk-grid-margin"></div>
                  <div  class="uk-width-1-1">
                        <label class="uk-form-label" for="form-stacked-text">Mongo Base Query <span v-on:click="formatJson1()"><span uk-icon="settings"></span></span></label>
                        <textarea class="uk-textarea" rows="15" placeholder="" id="baseQuery"  v-model="reportObject.baseQuery"></textarea>
                  </div>

                  <div  class="uk-width-1-1">
                        <label class="uk-form-label" for="form-stacked-text">Report Parameters <span v-on:click="formatJson2()"><span uk-icon="settings"></span></span></label>
                        <textarea class="uk-textarea" rows="15" placeholder="" id="reportParametersJson"  v-model="reportObject.reportParametersJson"></textarea>
                  </div>

                  <div class="uk-margin uk-grid-margin uk-first-column">
                      <div class="uk-inline">
                          <a tabindex="6" id="saveButton" class="clickable-btn uk-button uk-button-danger uk-button-small uk-grid-margin uk-first-column end-call-button" style="background-color: rgb(2 119 254); border-radius: 2px; place-self: center start; place-items: center; min-width: 100px; font-size: 0.65rem; line-height: 32px; font-weight: normal !important;" v-on:click="save()" >Save</a>
                          &nbsp;&nbsp;&nbsp;
                          <!--<a tabindex="7" class="clickable-btn uk-button uk-button-danger uk-button-small uk-first-column end-call-button" style="background-color: rgba(253, 253, 253, 0.99); color: rgb(111, 111, 111); border: 1px solid rgb(183, 183, 183); border-radius: 3px; place-self: center start; place-items: center; min-width: 100px; font-size: 0.65rem; line-height: 32px; font-weight: normal !important;" v-on:click="goBack()" >Exit</a>-->
                      </div>
                  </div>
                </form>

            </div>
      </div>
    </div>
</template>


<script>



import userMixinLib               from './../../mixins/lib/user_mixin_lib';
import utilsMixinLib              from './../../mixins/lib/utils_lib';
import uiListMixinLib             from './../../mixins/lib/ui-list.js';

    export default{
        mixins :    [
                        userMixinLib,
                        utilsMixinLib,
                        uiListMixinLib
                    ],
        props: ["id"],
        data: function () {
            return {
                appName: "taskone",
                tenantName : this.$route.params.tenant_name,
                tenantId : this.$route.params.tenant_id,
                tenantName : this.$route.params.tenant_name,
                reportObject :{
                    id : this.id,
                },
                availableReportGroups : []
            };
        },
        methods: {
            formatJson1(){
              document.getElementById("baseQuery").value = JSON.stringify(JSON.parse(document.getElementById("baseQuery").value), undefined, 4);
            },
            formatJson2(){
              document.getElementById("reportParametersJson").value = JSON.stringify(JSON.parse(document.getElementById("reportParametersJson").value), undefined, 4);
            },
            async getAllReportGroups() {

              const url = "./report-groups/list/all/1";
              let response = await fetch(url);
              let responseJson= await response.json();
              let availableReportGroups = responseJson;
              const availableReportGroupsUpdated = [];

              availableReportGroups.forEach((reportGroup) => {
                  const reportGroupObj = {
                    id: reportGroup.id,
                    name: reportGroup.name,
                    isSelected: false // Set false by default, so that if the category didn't have any member, then all of the available list could be passed to selectbox.
                  };

                  if (this.reportObject.reportGroupId !== null) {
                     if (reportGroup.id === this.reportObject.reportGroupId) {
                       reportGroup.isSelected = true;
                     }
                  }
                  availableReportGroupsUpdated.push(reportGroup);
              });

              this.availableReportGroups = availableReportGroupsUpdated;
            },

            updateReportGroupEmitEvent(data){
                if (data != null) { this.reportObject.reportGroupId = data.id; } else { this.reportObject.reportGroupId = null; }
            },

            goBack(){
              this.$router.push({
                  name: 'tenantUsers-list',
                  params: {
                  }
              });
            },
            doValidation(){

                console.log("this.reportObject = ", this.reportObject);
                if(this.reportObject.name===null)
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span> Please select a valid name.", {
                                          status: 'danger',
                                          pos: 'bottom-left',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.reportObject.description.trim()==="")
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span> Please enter a valid description.", {
                                          status: 'danger',
                                          pos: 'bottom-left',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.reportObject.reportFileName.trim()==="")
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span> Please enter a valid report file name.", {
                                          status: 'danger',
                                          pos: 'bottom-left',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.reportObject.status===null)
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span> Please select a valid account status.", {
                                          status: 'danger',
                                          pos: 'bottom-left',
                                          timeout: 5000
                                      });

                      return false;
                }

                return true;
            },
            save(){

                if(!this.doValidation())
                    return false; // stop here if form is invalid.

                // this.formatJson();

                let btnText = document.getElementById("saveButton").innerHTML;
                document.getElementById("saveButton").innerHTML = "Saving..";
                this.disableHTMLElement(document.getElementById("saveButton"));

                let post_url = "./reports/save";
                console.log("Posting data to : " + post_url);

                let form = {
                    id :      this.reportObject.id,
                    // tenantId : this.tenantId,
                    name :   this.reportObject.name,
                    status  :   this.reportObject.status,
                    reportGroupId : this.reportObject.reportGroupId,
                    description :this.reportObject.description,
                    reportFileName :this.reportObject.reportFileName,
                    baseQuery: this.reportObject.baseQuery,
                    reportParametersJson: this.reportObject.reportParametersJson
                }

                console.log("Form data : ", form);

                // VueJS ajax call-1
                axios.post(post_url, form)
                          .then((dataResponse) => {

                            console.log("TenantUser Registration Result : ");
                            console.log(dataResponse);

                            if(dataResponse.data["actionResult"]===1)
                            {
                                this.newTenantUserInfo = dataResponse.data["bean"];

                                UIkit.notification(`<div class="taskone-notification">
                                                        <span uk-icon="icon: check;ratio:1"></span>
                                                        <div> Report modified successfully. </div>
                                                    </div>`, {
                                                    status: 'success',
                                                    pos: 'bottom-left',
                                                    timeout: 500000
                                                });

                                document.getElementById("saveButton").innerHTML = btnText;
                                this.enableHTMLElement(document.getElementById("saveButton"));
                                this.$emit("refreshList",{});
                            }
                            else
                            {
                                let errorMsg = (dataResponse["body"])["message"];
                                UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span>" + errorMsg, {
                                                    status: 'danger',
                                                    pos: 'top-right',
                                                    timeout: 5000
                                                });


                                document.getElementById("saveButton").innerHTML = btnText;
                                this.enableHTMLElement(document.getElementById("saveButton"));
                                return false;
                            }


                    })
                    .catch(function(errorResponse) {

                        console.log("ERROR MS - ", errorResponse);
                        let exceptionMsg = errorResponse.body["exception"];

                        UIkit.notification("<span uk-icon='icon: warning ;ratio: 0.75'></span> " + exceptionMsg + ".", {
                                            status: 'danger',
                                            pos: 'top-right',
                                            timeout: 500000
                                        });


                        document.getElementById("saveButton").innerHTML = btnText;
                        this.enableHTMLElement(document.getElementById("saveButton"));
                        return false;
                    });
            },

            // Handle the events emitted when selectbox component values are modified.
            updateMembersEmitEvent(data){
                const memberIds = Array.from(data).map(item => item.id);
                this.reportObject["memberIds"] = memberIds;
            },
            updateAdminsEmitEvent(data){
                const adminIds = Array.from(data).map(item => item.id);
                this.reportObject["adminIds"] = adminIds;
            },
            getRecord(){

                let get_url = "./reports/get/" + this.reportObject.id;

                axios.get(get_url)
                        .then((dataResponse) => {
                            if(dataResponse.data["actionResult"]==1)
                            {
                                let reportObject = dataResponse.data["bean"];
                                this.reportObject = reportObject;
                                document.getElementById("baseQuery").textContent = JSON.stringify(this.reportObject.baseQuery, undefined, 4);
                                document.getElementById("reportParametersJson").textContent = JSON.stringify(this.reportObject.reportParametersJson, undefined, 4);
                                this.getAllReportGroups();
                            }
                        })
                        .catch((error) => {

                            UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span>" + error, {
                                                status: 'danger',
                                                pos: 'bottom-left',
                                                timeout: 5000
                                            });
                            return false;
                        });


            },

        },
        mounted: function () {

            // this.getAllReportGroups();

        },
        created: function () {
            console.log("Edit tenantUser form");
        },
        watch: {

          // Since we are passing an asynced data as 'availableItems', it is necessary to watch this prop when it receives new data.
          id:function(newId, oldId){
            console.log("id changed from " + oldId + " to " + newId);
            this.reportObject.id = newId;
            this.getRecord();
          }
        }
    }

</script>
