<style scoped>
  .uk-dropdown-nav {
      font-size: .975rem;
  }
</style>


<template>


    <div class="" style="background: aliceblue;display:grid;grid-template-columns:1fr;width:100%;place-items:start center">


        <div class="stage" style="display: grid;grid-template-columns:1fr 1fr">
            <div class="" style="padding-top: 50px;width: 65%;place-self: start end;">
                <form class="uk-grid-small" uk-grid style="font-family:lato" onsubmit="return false;">


                  <div class="uk-width-1-1">
                      <div class="column" style="
                                              padding: 10px 50px 20px 0px;
                                              padding-left: 0px;padding-top: 10px;
                                              font-weight: 300;
                                              font-size: 16px;
                                              border-bottom: 1px solid #8080802e;font-family:Raleway
                                          ">
                                        Create a New Role
                        </div>
                  </div>


                  <div class="uk-width-1-1">
                      <label class="uk-form-label" for="form-stacked-text">Role Name</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <span class="uk-form-icon" uk-icon="icon: nut"></span>
                              <input id="name" type="text" class="uk-input"  placeholder="Name"  v-model="roleObject.name" style="text-transform:uppercase;font-family: Lato;font-weight: 300;height: 35px;" autofocus="true">
                          </div>
                      </div>
                  </div>


                  <div  class="uk-width-1-1">
                      <label class="uk-form-label" for="form-stacked-text">Status</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <span class="uk-form-icon" uk-icon="icon: lock"></span>
                              <input id="name" type="text"  class="uk-input"  placeholder="Status"  v-model="roleObject.status" style="font-family: Lato;font-weight: 300;height: 35px;" autofocus="true">
                          </div>
                      </div>
                  </div>


                  <div  class="uk-width-1-1">
                        <label class="uk-form-label" for="form-stacked-text">Provide a short description</label>
                        <textarea class="uk-textarea" rows="5" placeholder="" id="description"  v-model="roleObject.description"></textarea>
                  </div>

                  <div class="uk-margin">
                      <div class="uk-inline">
                        <button id="registerNewButton" class="uk-button uk-button-danger uk-button-small ui_button_medium" v-on:click="save()"  style="width: 150px;color:white!important">Update Role</button>
                        &nbsp;&nbsp;&nbsp;
                        <button class="uk-button uk-button-default uk-button-small ui_button_medium" v-on:click="goToHome()" style="width:70px">Exit</button>
                      </div>
                  </div>
                </form>

            </div>
        </div>
    </div>
</template>


<script>



import userMixinLib                   from './../../mixins/lib/user_mixin_lib';
import utilsMixinLib                  from './../../mixins/lib/utils_lib';
    export default{
        mixins :    [
                        userMixinLib,
                        utilsMixinLib
                    ],
        props: [],
        data: function () {
            return {
                appName: "/oct.io",
                roleObject :{},
                userId : null,
                username : null
            };
        },
        methods: {

            goToHome: function(){

                this.$router.push({
                    name: 'role-list',
                    params: {}
                });
            },
            doValidation : function()
            {
              try{

                  if(this.roleObject.name===undefined || this.roleObject.name.trim()==="")
                  {
                        UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Please enter a valid Name.", {
                                            status: 'danger',
                                            pos: 'top-center',
                                            timeout: 5000
                                        });

                        return false;
                  }
                  else if(this.roleObject.status===undefined || this.roleObject.status.trim()==="")
                  {
                        UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Please enter a valid Status.", {
                                            status: 'danger',
                                            pos: 'top-center',
                                            timeout: 5000
                                        });

                        return false;
                  }

                  return true;
              }
              catch(e)
              {
                alert(e);
              }

            },
            save : function(){

                if(!this.doValidation())
                    return false; // stop here if form is invalid.

                let btnText = document.getElementById("registerNewButton").innerHTML;
                document.getElementById("registerNewButton").innerHTML = "Saving..";
                this.disableHTMLElement(document.getElementById("registerNewButton"));

                let _this_component = this;
                let post_url = "./../roles/save";
                console.log("Posting data to : " + post_url);




                let form = {
                    id :     "new",
                    name :   this.roleObject.name,
                    status  :   this.roleObject.status,
                    description     :   this.roleObject.description
                }

                let formString = JSON.stringify(form,null,null);


                // VueJS ajax call-1
                axios.post(post_url, form)
                          .then((dataResponse) => {

                            console.log("Role save result : ");
                            console.log(dataResponse);

                            if(dataResponse.data["actionResult"]===1)
                            {
                                _this_component.newUserInfo = dataResponse.data["bean"];

                                UIkit.notification("<span uk-icon='icon: check;ratio: 0.65'></span> New Role added successfully",{
                                    status: 'success',
                                    pos: 'top-right',
                                    timeout: 5000
                                });

                                document.getElementById("registerNewButton").innerHTML = btnText;
                                this.enableHTMLElement(document.getElementById("registerNewButton"));
                            }
                            else
                            {
                                alert("Error : " + dataResponse)
                            }


                    })
                    .catch(function(errorResponse) {

                        console.log("ERROR MS - ", errorResponse);
                        let exceptionMsg = errorResponse.body["exception"];

                        UIkit.notification("<span uk-icon='icon: warning ;ratio: 0.65'></span> " + exceptionMsg + ".", {
                                            status: 'danger',
                                            pos: 'top-right',
                                            timeout: 500000
                                        });


                        document.getElementById("registerNewButton").innerHTML = btnText;
                        this.enableHTMLElement(document.getElementById("registerNewButton"));
                        return false;
                    });
            },
            logInfo: function (msg){

                let consoleLog = this.consoleLogTxt + " \n " + msg;
                this.consoleLogTxt = consoleLog;

                if (this.displayConsoleLog)
                {
                    console.log(msg);
                }
            },
        },
        mounted: function () {
        },
        created: function () {
            console.error("WELCOME TO NEW Role REGISTRATION FORM.");
        }
    }

</script>
