<style scoped>
  .uk-dropdown-nav {
      font-size: .975rem;
  }
</style>


<template>

  <div style="background: whitesmoke;;display:grid;grid-template-columns:1fr;width:100%;place-items:start center;overflow-y:overlay" class="custom-scroll-bar">


      <div class="stage" style="display: grid;place-items: start center;">
          <div class="" style="padding-top:50px;width: 50%;">

              <form class="uk-grid-small" uk-grid style="font-family:lato" onsubmit="return false;">

                  <div class="uk-width-1-1">
                      <div class="column" style="
                                              padding: 10px 50px 20px 0px;
                                              padding-left: 0px;padding-top: 10px;
                                              font-weight: 300;
                                              font-size: 16px;
                                              border-bottom: 1px solid #8080802e;font-family:Raleway
                                          ">
                                          Modify Tenant
                        </div>
                  </div>


                  <div class="uk-width-1-1">
                      <label class="uk-form-label" for="form-stacked-text">Name</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <span class="uk-form-icon" uk-icon="icon: user"></span>
                              <input id="name" type="text" class="uk-input"  placeholder="Name"  v-model="tenantObject.name" style="font-family: Lato;font-weight: 300;height: 35px;" autofocus="true">
                          </div>
                      </div>
                  </div>

                  <div class="uk-width-1-2@s">
                      <label class="uk-form-label" for="form-stacked-text">Domain Email</label>
                      <div class="uk-form-controls">
                        <div class="uk-inline uk-width-1-1">
                            <span class="uk-form-icon" uk-icon="icon: mail"></span>
                            <input id="email" type="text"  class="uk-input"  placeholder="@yourcompany.com"  v-model="tenantObject.domainEmail" style="font-family: Lato;font-weight: 300;height: 35px;" autofocus="true">
                        </div>
                      </div>
                  </div>

                  <div  class="uk-width-1-2@s">
                      <label class="uk-form-label" for="form-stacked-text">Status</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <select  class="uk-select" name="status" v-model="tenantObject.status" style="font-family: Lato;font-weight: 300;height: 35px;">
                                  <option value="DEMO">DEMO</option>
                              </select>
                          </div>
                      </div>
                  </div>

                  <div class="uk-width-1-2@s">
                      <label class="uk-form-label" for="form-stacked-text">Maximum Users</label>
                      <div class="uk-form-controls">
                        <div class="uk-inline uk-width-1-1">
                            <input id="maxNumberOfUsers" type="number"  class="uk-input"  placeholder=""  v-model="tenantObject.maxNumberOfUsers" style="font-family: Lato;font-weight: 300;height: 35px;" autofocus="true">
                        </div>
                      </div>
                  </div>

                  <div class="uk-width-1-2@s">

                  </div>


                  <div class="uk-margin">
                      <div class="uk-inline">
                          <button id="saveButton" class="clickable-btn uk-button uk-button-danger uk-button-small uk-grid-margin uk-first-column end-call-button" style="background-color: rgb(240, 80, 110); border-radius: 2px; place-self: center start; place-items: center; min-width: 100px; font-size: 0.65rem; line-height: 32px; font-weight: normal !important;" v-on:click="save()" >Update</button>
                          &nbsp;&nbsp;&nbsp;
                          <button class="clickable-btn uk-button uk-button-danger uk-button-small uk-first-column end-call-button" style="background-color: rgba(253, 253, 253, 0.99); color: rgb(111, 111, 111); border: 1px solid rgb(183, 183, 183); border-radius: 3px; place-self: center start; place-items: center; min-width: 100px; font-size: 0.65rem; line-height: 32px; font-weight: normal !important;" v-on:click="goToHome()" >Exit</button>
                      </div>
                  </div>

                  <div class="uk-width-1-1">
                      <div><br>Other Options for Tenant<hr></div>
                      <div class="uk-margin">
                          <div class="uk-inline">
                              <button id="createDefaultCategoryButton" class="clickable-btn uk-button uk-button-success uk-button-small uk-grid-margin uk-first-column end-call-button" style="background-color: rgb(238 253 9);border-radius: 2px;place-self: center start;place-items: center;min-width: 100px;font-size: 0.65rem;line-height: 32px;font-weight: normal !important;" v-on:click="createDefaultCategory()" >Create Default Category</button>
                          </div>
                      </div>
                  </div>



                </form>

            </div>
        </div>
    </div>
</template>


<script>



import userMixinLib                   from './../../mixins/lib/user_mixin_lib';
import utilsMixinLib                  from './../../mixins/lib/utils_lib';
    export default{
        mixins :    [
                        userMixinLib,
                        utilsMixinLib
                    ],
        props: [],
        data: function () {
            return {
                appName: "/oct.io",
                tenantObject :{
                    id : this.$route.params.id,
                },
                userId : null,
                username : null
            };
        },
        methods: {

            goToHome: function(){

                this.$router.push({
                    name: 'tenant-list',
                    params: {}
                });
            },
            doValidation : function()
            {

                if(this.tenantObject.name.trim()==="")
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Please enter a valid Name.", {
                                          status: 'danger',
                                          pos: 'top-center',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.tenantObject.domainEmail.trim()==="") // || (this.email.trim()!=="" && this.email.indexOf("@")===-1))
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Please enter a valid email domain.", {
                                          status: 'danger',
                                          pos: 'top-center',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.tenantObject.status.trim()==="")
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Please enter a valid Status.", {
                                          status: 'danger',
                                          pos: 'top-center',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.tenantObject.maxNumberOfUsers>1000)
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Maximum Users allowed is 1000", {
                                          status: 'danger',
                                          pos: 'top-center',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.tenantObject.maxNumberOfConferences>1000)
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.65'></span> Maximum Conferences allowed is 1000", {
                                          status: 'danger',
                                          pos: 'top-center',
                                          timeout: 5000
                                      });

                      return false;
                }

                return true;
            },
            getRecord : function(){

                console.log("getRecord");
                let _this_component = this;

                if(_this_component.tenantObject.id === undefined)
                {
                    alert("Tenant ID Invalid");
                    return false;
                }

                let get_url = "./tenants/get/" + _this_component.tenantObject.id;
                console.log(get_url);

                axios.get(get_url)
                        .then(function (dataResponse) {

                            console.log(dataResponse);
                            if(dataResponse.data["actionResult"]==1)
                            {
                                let tenantObject = dataResponse.data["bean"];
                                _this_component.tenantObject = tenantObject;
                                return false;
                            }

                        });


            },
            save : function(){

                if(!this.doValidation())
                    return false; // stop here if form is invalid.


                let btnText = document.getElementById("saveButton").innerHTML;
                document.getElementById("saveButton").innerHTML = "Loading.. Please wait.";
                this.disableHTMLElement(document.getElementById("saveButton"));

                let _this_component = this;
                let post_url = "./../tenants/save";
                console.log("Posting data to : " + post_url);


                if(_this_component.tenantObject.id === undefined)
                {
                    alert("Tenant ID Invalid");
                    return false;
                }

                let form = {
                    id :      this.tenantObject.id,
                    name :   this.tenantObject.name,
                    status  :   this.tenantObject.status,
                    domainEmail     :   this.tenantObject.domainEmail,
                    maxNumberOfUsers : this.tenantObject.maxNumberOfUsers,
                    maxNumberOfConferences : this.tenantObject.maxNumberOfConferences,
                }

                let formString = JSON.stringify(form,null,null);

                console.log("Making axios.post..");
                // VueJS ajax call-1
                axios.post(post_url,form)// as axios takes the jsondata directly  formString)
                          .then((dataResponse) => {

                            console.log("User Registration Result : ");
                            console.log(dataResponse);

                            if(dataResponse.data["actionResult"]===1)
                            {
                                _this_component.newUserInfo = dataResponse.data["bean"];

                                UIkit.notification("<span uk-icon='icon: check;ratio: 0.65'></span> Tenant updated successfully",{
                                    status: 'success',
                                    pos: 'top-right',
                                    timeout: 5000
                                });

                                document.getElementById("saveButton").innerHTML = btnText;
                                this.enableHTMLElement(document.getElementById("saveButton"));
                            }
                            else
                            {
                                alert("Error : " + dataResponse)
                            }


                    })
                    .catch(function(errorResponse) {

                        console.log("ERROR MS - ", errorResponse);
                        let exceptionMsg = errorResponse.body["exception"];

                        UIkit.notification("<span uk-icon='icon: warning ;ratio: 0.65'></span> " + exceptionMsg + ".", {
                                            status: 'danger',
                                            pos: 'top-right',
                                            timeout: 500000
                                        });


                        document.getElementById("saveButton").innerHTML = btnText;
                        this.enableHTMLElement(document.getElementById("saveButton"));
                        return false;
                    });
            },
            createDefaultCategory : function(){

                // let btnText = document.getElementById("createDefaultButton").innerHTML;
                // document.getElementById("saveButton").innerHTML = "Loading.. Please wait.";
                // this.disableHTMLElement(document.getElementById("saveButton"));

                let _this_component = this;
                let post_url = "./../tenants/create-default-category";
                console.log("Posting data to : " + post_url);


                if(_this_component.tenantObject.id === undefined)
                {
                    alert("Tenant ID Invalid");
                    return false;
                }

                let form = {
                    id :      this.tenantObject.id,
                }

                let formString = JSON.stringify(form,null,null);

                console.log("Making axios.post..");
                // VueJS ajax call-1
                axios.post(post_url,form)// as axios takes the jsondata directly  formString)
                          .then((dataResponse) => {

                            console.log("Default Category Creation Result : ");
                            console.log(dataResponse);

                            if(dataResponse.data["actionResult"]===1)
                            {
                                _this_component.newUserInfo = dataResponse.data["bean"];

                                UIkit.notification("<span uk-icon='icon: check;ratio: 0.65'></span> Tenant default-category created successfully",{
                                    status: 'success',
                                    pos: 'top-right',
                                    timeout: 5000
                                });

                                // document.getElementById("saveButton").innerHTML = btnText;
                                // this.enableHTMLElement(document.getElementById("saveButton"));
                            }
                            else
                            {
                                alert("Error : " + dataResponse)
                            }


                    })
                    .catch(function(errorResponse) {

                        console.log("ERROR MS - ", errorResponse);
                        let exceptionMsg = errorResponse.body["exception"];

                        UIkit.notification("<span uk-icon='icon: warning ;ratio: 0.65'></span> " + exceptionMsg + ".", {
                                            status: 'danger',
                                            pos: 'top-right',
                                            timeout: 500000
                                        });


                        // document.getElementById("saveButton").innerHTML = btnText;
                        // this.enableHTMLElement(document.getElementById("saveButton"));
                        return false;
                    });
            },
            logInfo: function (msg){

                let consoleLog = this.consoleLogTxt + " \n " + msg;
                this.consoleLogTxt = consoleLog;

                if (this.displayConsoleLog)
                {
                    console.log(msg);
                }
            },
        },
        mounted: function () {
            this.getRecord();
        },
        created: function () {
            console.error("WELCOME TO NEW USER REGISTRATION FORM.");
        }
    }

</script>
